  --button-disabled-color: #1118;
  --accent-color: #2f2c;
  --dark-color: #181c;
  --text-color: #fffe;
  --text-hint-color: #fff4;

  --border-smooth: 0.3rem;
  
  color: #fffe;
  
  color: var(--text-color);
}

*::-webkit-scrollbar {
  width: 4px;
  height: 4px;
}
 
*::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}
 
*::-webkit-scrollbar-thumb {
  background-color: #2f2c;
  border: 1px solid #181c;
  border-radius: 0.5em;
}

.container {
  padding: 2rem;
}

.screen {
  height: 100vh;
}

.form-group {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 1rem;
}

.card {
  background-color: #111c;
  border-radius: 0px;
  padding: 1rem;
  border: 1px solid #2f2c;
  border: 1px solid var(--accent-color);
  min-width: 200px;
  box-shadow: inset 0px 0px 0px #0000;
  transition: all 400ms ease-in-out;
  border-radius: 0.3rem;
  border-radius: var(--border-smooth);
}

.card--sm {
  padding: 0.4rem;
}

.card:hover {
  box-shadow: inset 0px 0px 40px #2f25;
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
}

.card-enter {
  opacity: 0;
  -webkit-transform: scale(.8);
          transform: scale(.8)
}
.card-enter-active {
  opacity: 1;
  -webkit-transform: scale(1);
          transform: scale(1);
}
.card-exit {
  opacity: 1;
  -webkit-transform: scale(1);
          transform: scale(1);
}
.card-exit-active {
  opacity: 0;
  -webkit-transform: scale(.8);
          transform: scale(.8);
}

.input-list-item-enter {
  -webkit-transform: scale(0);
          transform: scale(0)
}
.input-list-item-enter-active {
  -webkit-transform: scale(1);
          transform: scale(1);
  transition: all 400ms ease-in-out;
}
.input-list-item-exit {
  -webkit-transform: scale(1);
          transform: scale(1);
}
.input-list-item-exit-active {
  -webkit-transform: scale(0);
          transform: scale(0);
  transition: all 400ms ease-in-out;
}

.avatar {
  border-radius: 50%;
  width: 2rem;
  height: 2rem;
  object-fit: cover;
  object-position: center;
}

.avatar--big {
  width: 8rem;
  height: 8rem;
}

.float-menu {
  position: absolute;
  top:100%;
  right:0;
}

.ReactModal__Overlay {
  opacity: 0;
  transition: opacity 300ms ease-in-out;
}
.ReactModal__Overlay--after-open {
  opacity: 1;
}
.ReactModal__Overlay--before-close {
  opacity: 0;
}

.ReactModal__Content {
  opacity: 0;
  -webkit-transform: translate(-50%, -100%) scale(0.9);
          transform: translate(-50%, -100%) scale(0.9);
  transition: all 300ms ease-in-out;
  margin-left: 50%;
  margin-top: 50vh;
}

.ReactModal__Content--after-open {
  opacity: 1;
  -webkit-transform: translate(-50%, -50%) scale(1);
          transform: translate(-50%, -50%) scale(1);
}
.ReactModal__Content--before-close {
  opacity: 0;
  -webkit-transform: translate(-50%, -100%) scale(0.9);
          transform: translate(-50%, -100%) scale(0.9);
}

h1, h2, h3 {
  text-transform: uppercase;
  color: #2f2c;
